.header-content {
  width: 78%;
  margin: auto;
  z-index: 100;
}
.header-footer {
  position: relative;
  margin-top: 60px;
  padding-top: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 360px;

  justify-content: space-between;
}

.contact-us {
  color: white;
  font-family: "Montserrat";
  font-style: normal;
  font-weight: bold;
  font-size: 80px;
  padding: 20px 0px;
  text-align: center;
  line-height: 98px;
}

.navbar {
  z-index: 100;
  display: flex;
  justify-content: space-between;
  height: 149px;
}
.logo-container {
  height: 149px;
}

.nav-item {
  color: white;
}
.nav-item:hover {
  cursor: pointer;
  border-bottom: 1px solid white;
  transition: all ease-in-out 1s;
  color: #eee;
  transform: scaleZ(3);
}
.active {
  border-bottom: 1px solid white;
}
a {
  text-decoration: none;
}

.nav-items {
  width: 738px;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header-banner {
  text-align: left;
  margin-top: 15vh;
}

.banner-line {
  font-size: 25px;
  line-height: 30px;
  letter-spacing: 0.1em;
  color: white;
  font-family: "Montserrat";
}
.line-1 {
  font-family: "Montserrat";
  font-size: 40px;
  line-height: 55px;
}
.line-2 {
  font-family: adobe-garamond-pro, serif;
  font-weight: bold;
  font-size: 100px;
  line-height: 100px;
  letter-spacing: 0.07em;
}
.line-3 {
  font-family: adobe-garamond-pro, serif;
  font-size: 50px;
  letter-spacing: 0.3em;
  line-height: 54px;
}
.w {
  width: 600px;
}

.button {
  background-color: #e0b973;
  border-radius: 10px;

  display: flex;
  align-items: center;
  justify-content: center;
}

.button:hover {
  cursor: pointer;
  transition: all ease-in-out 0.5s;
  transform: scaleZ(3);
}
.button-text {
  margin-left: 10px;
  color: #fff;
  font-weight: bold;
  font-size: 20px;
  line-height: 50px;
}
.normal {
  width: 240px;
  height: 65px;
}
.small {
  width: 175px;
  height: 40px;
}
.fit {
  margin-right: 5px;
  padding: 5px 7px;

  width: -webkit-fit-content;

  width: -moz-fit-content;

  width: fit-content;
}
.fit div {
  display: none;
}
.price {
  color: white;
  font-size: 20px;
}

.scroll-button {
  margin-top: 5%;
  position: absolute;
  bottom: 0;

  color: white;
  font-weight: 700;
  font-size: bold;
}
.scroll-button:hover {
  cursor: pointer;
  color: #eee;
}
.scroll-icon {
  margin-top: 5px;
  width: 35px;
}

